/* src/TodoList.css */

.todo-container {
    flex-direction: column;
    width: 100%;
    height: 100vh;
    background-image: url("https://e0.pxfuel.com/wallpapers/975/575/desktop-wallpaper-artstation-death-note-gabriel-marcondes-death-note-book.jpg");
    background-size: cover; 
    background-position: center; 
    background-repeat: no-repeat;
    min-height: 100vh;
    padding: 20px;
    box-sizing: border-box; /*  */
    justify-content: center;
    align-items: center;
    
  }
  
  
  .todo-list {
    flex: 1;
    width: 100%;
    list-style: none;
    padding: 0;
    margin: 0;
    background-color: #218838;
  }
  
  .todo-item {
    display: flex;
    justify-content: space-between;
    align-items: center;
    padding: 10px;
    border-bottom: 1px solid #ffffff;
    font-size: 16px;
    transition: background-color 0.3s;
    align-items: center;
    font-weight: bold;
  }
  
  .todo-item.completed {
    color: #000000;
    color: white;
  }
  
  .todo-item:hover {
    background-color: #f1f1f141;
  }
  
  .todo-complete-button,
  .todo-remove-button {
    padding: 5px 10px;
    background-color: #007BFF;
    color: white;
    border: none;
    border-radius: 5px;
    cursor: pointer;
    transition: background-color 0.3s;
    margin: 0.5rem;
  }
  
  .todo-remove-button {
    background-color: #dc3545;
  }
  
  .todo-complete-button:hover{
    background-color: #0056b3;
  }
  
  .todo-remove-button:hover{
    background-color: #ad2d3a;
  }
  
  .content {
    display: flex;
    position: fixed;
    justify-content: space-between;
    width: 97%;
    justify-content: center;
    background-color: rgba(0, 0, 0, 0.541);
    margin-top: 1rem;
  }
  
  .todo-input-container {
    display: flex;
    justify-content: space-between;
    align-items: center;
    width: 100%;
    padding: 10px;
    bottom: 0;
    left: 0;
    box-shadow: 0px -2px 10px rgba(0, 0, 0, 0.3);
  }
  
  .todo-input {
    flex: 1;
    padding: 10px;
    font-size: 16px;
    border: 1px solid #ccc;
    border-radius: 5px;
    margin-right: 10px;
    width: 65rem;
  }
  
  .todo-button img {
    width: 65px;
    border-radius: 0.3rem;
    margin-top: 0.3rem;
    box-shadow: 0px 0px 10px rgb(0, 0, 0);
    border: 1px solid white;
  }
  
  .todo-button img:hover {
    box-shadow: 0px 0px 10px rgb(255, 255, 255);
  }
  
  .all-task{
    display: flex;
    align-items: center;
  }
  
  .task{
    width: 90%;
    background-color: #0000001c;
    box-shadow: 0px 0px 10px rgba(0, 0, 0, 0.61);
    margin: 0.5rem;
    color: white;
    border-radius: 0.5rem;
  }
  
  .task:hover{
    box-shadow: 0px 0px 10px black;
    border: none;
  }
  
  .sub-task{
    overflow-y: auto;
    height: 60vh;
    margin-top: 0;
    margin-top: 0.5rem;
    margin-right: 1rem;
    scrollbar-color: red;
    width: 90%;
  
  }
  
  h1{
    color: #fffcfc;
    text-align: center;
  }
  
  .todo-button img{
    width: 65px;
    border-radius: 0.3rem;
    margin-top: 0.3rem;
    box-shadow: 0px 0px 10px rgb(0, 0, 0);
    border: 1px solid white;
  }
  
  
  .todo-button img:hover{
    box-shadow: 0px 0px 10px rgb(255, 255, 255);
  }
  
  .todo-button{
    background-color: #ffffff00;
    border: none;
  }
  
  .button-container {
  margin-bottom: 20px;
  display: flex;
  position: relative;
  justify-content: center;
  align-items: center;
  }
  
  
  /* src/TodoList.css */
  
  .toggle-button {
  padding: 10px 20px;
  font-size: 1em;
  color: rgb(0, 0, 0);
  border: none;
  border-radius: 5px;
  cursor: pointer;
  transition: background-color 0.3s ease-in-out;
  margin: -3px 10px;
  background-color: rgba(255, 255, 255, 0.281);
  border: 1px solid black;
  backdrop-filter: blur(2px);
  }
  
  .toggle-button:hover {
  background-color: #0056b3;
  color: white;
  }
  
  .toggle-button.active {
    background-color: #007bff9f;
    color: white;
  }
  
  .task-sections{
    align-items: center;
    justify-content: center;
    display: flex;
    margin: 0%;
    padding: 0%;
    margin-top: -1rem;
    margin-bottom: -1rem;
  }
  
  .butt{
    margin: 0%;
  }
  
  .butt img{
    width: 15px;
    display: none;
  }
  
  .sub-task img{
    display: none;
  }
  
  @media (max-width: 768px){
  
    .todo-container {
        background-image: url("https://i.redd.it/x3ccz198vh451.jpg");
        background-size: cover; 
        background-position: center; 
        background-repeat: no-repeat;
        box-sizing: border-box; /*  */
        justify-content: center;
        align-items: center;
        
    }
  
    .todo-input {
        flex: 1;
        padding: 10px;
        font-size: 16px;
        border: 1px solid #ccc;
        border-radius: 5px;
        margin-right: 10px;
        width: 12rem;
    }
  
    .content {
        display: flex;
        position: fixed;
        justify-content: space-between;
        width: 90%;
        justify-content: center;
        background: none;
        margin-top: 1rem;
        align-items: center;
    }
  
    .todo-button img{
        width: 60px;
        border-radius: 0.3rem;
        margin-top: 0.3rem;
        box-shadow: 0px 0px 10px rgb(0, 0, 0);
        border: 1px solid white;
      }
    
      .task{
        width: 100%;
        margin-top: 5%;
      }
  
      .todo-item {
        display: flex;
        justify-content: space-between;
        border-bottom: 1px solid #ffffff;
        transition: background-color 0.3s;
        margin-left: -2rem;
        width: 100%;
    }
  
    .toggle-button{
        color: white;
        border: 1px solid white;
    }
    
    .todo-complete-button,
    .todo-remove-button {
    padding: 5px 10px;
    background-color: #007bffa1;
    color: white;
    border: none;
    border-radius: 5px;
    cursor: pointer;
    transition: background-color 0.3s;
    margin: 0.5rem;
  }
  .todo-remove-button {
    background-color: #dc354685;
  }
  
  .butt img{
    width: 20px;
    display: flex;
  }
  
  .butt button{
    display: none;
    width: 2rem;
  }
  
  .butt{ 
    display: flex;
    justify-content: space-between;
  }
  
  .sub-task img{
    display: flex;
    width: 20px;
    margin: 0px 5px 0px 5px;
  }
  
  .sub-task button{
    display: none;
  }
  
  }